@mixin sm {
  @media (max-width: 450px) { @content; }
}
@mixin md {
  @media (min-width: 768px) { @content; }
}
@mixin lg {
  @media (min-width: 1200px) { @content; }
}
@mixin xl {
  @media (min-width: 1920px) { @content; }
}
@mixin max-md {
  @media (max-width: 767px) { @content; }
}


